:root {
    --fontColor: rgb(194 147 102);
    --btnColor: rgb(41 37 36);
    --description: rgb(187, 169, 151);
}

.camera {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    overflow: hidden;
}

.camera h1 {
    /* margin-top: 0; */
    color: var(--fontColor);
}

.cameraError {
    position: relative;
    z-index: 5;
    margin-top: 5vh;

    display: flex;
    justify-content: center;
    align-items: center;

    max-width: 400px;
    height: 100%;

    /* border: 2px solid var(--fontColor); */
    border-radius: 10px;
}

.cameraError span {
    width: 80%;
    margin-top: 10vh;
    text-align: center;
    font-family: 'Product Sans';
    color: var(--description);
}

.arrowBack {
    position: relative;
    top: 3%;
    right: 45%;
    transform: translate(55%, 55%);
    transition: 0.15s ease-in-out;
}

.arrowBack:hover {
    filter: sepia(1) brightness(1);
}

.description {
    width: 80%;

    font-family: 'Product Sans';
    font-size: 18px;
    text-align: center;
    color: var(--description);
}

.video {
    position: relative;
}

.video video {
    /* border: 5px solid var(--fontColor); */
}

.screenshot {
    position: relative;
    z-index: 5;
    margin-top: 5vh;

    width: 100%;
    max-width: 400px;
    height: auto;

    /* border: 2px solid var(--fontColor); */
    border-radius: 10px;
}

.scanningLine {
    position: relative;
    bottom: 10px;
    z-index: 5;

    width: 100%;
    height: 5px;

    background-color: var(--fontColor);
    border-radius: 5px;
}

.videoCam {
    position: relative;
    border-radius: 10px;
    margin-top: 5vh;

    width: 100%;
    max-width: 400px;
    height: auto;

    transform-origin: center;
    aspect-ratio: 9/6 auto;
}

.hangLogo {
    display: block;
    position: absolute;
    top: 50%;
    left: 47%;
    transform: translate(-50%, -43%);

    margin: 0 auto;
    z-index: 5;
}

.hangLogo img {
    width: 20rem;
    height: 18rem;
    stroke-width: 0.5px;
    filter: invert(1) sepia(1) saturate(1000%) hue-rotate(310deg) brightness(70%)
        drop-shadow(2px 2px 5px var(--fontColor));
}

.screenBtn {
    cursor: pointer;

    font-family: 'Product Sans';
    font-size: 16px;

    width: 50%;
    height: 45px;
    background-color: var(--btnColor);
    border: none;
    border-radius: 0.5rem;
    margin-top: 4vh;

    color: var(--fontColor);
    transition: all 0.2s ease-in;

    /* &:hover {
        background-color: var(--fontColor);
        color: var(--btnColor);
    } */
}

.screenDiv {
    width: 220px;
    height: 220px;
}
